.container.addAddr {
  display: flex;
  flex-direction: column;
  flex: 1;
  background: #F7F7F7;
}
.container.addAddr .content {
  flex: 1;
  overflow-y: scroll;
}
.container.addAddr .content .addBox {
  background-color: white;
  border-radius: 0.5rem;
  margin: 0.5rem 0.5rem 0rem 0.5rem;
  padding: 0rem 0.8rem 0.8rem 0.75rem;
  overflow: hidden;
}
.container.addAddr .content .addBox .rowBox {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-top: 0.7rem;
}
.container.addAddr .content .addBox .rowBox .title {
  font-size: 0.65rem;
  color: #7B7B7B;
  width: 4.05rem;
}
.container.addAddr .content .addBox .rowBox > input {
  flex: 1;
  border: none;
  font-size: 0.65rem;
  color: #333;
}
.container.addAddr .content .addBox .rowBox > img {
  display: block;
  width: 0.3rem;
  height: 0.5rem;
}
.maskContent {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
}
.maskContent .maskClose {
  background-color: rgba(0, 0, 0, 0.4);
  flex: 1;
}
.maskContent .selectContent {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  width: 100%;
  height: 15rem;
  background-color: white;
}
.maskContent .selectContent .btnBox {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #ddd;
}
.maskContent .selectContent .btnBox > span {
  display: block;
  padding: 0.5rem 1rem 0.5rem 1rem;
}
.maskContent .selectContent .selectBox {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex: 1;
  height: 100%;
}
.maskContent .selectContent .selectBox .selectItem {
  flex: 1;
  height: 100%;
  overflow-y: scroll;
}
.maskContent .selectContent .selectBox .selectItem .scrollBox > p {
  text-align: center;
  color: #333;
  line-height: 1rem;
  margin: 0.25rem 0rem 0.25rem 0rem;
}
.maskContent .selectContent .selectBox .selectItem .scrollBox > p.active {
  color: red;
}
